.si__single[data-aos="fade-down-left"] {
  // or another animation type you have
  -webkit-transform: translate3d(
    300px,
    -350px,
    350px
  ); // or another value you need
  transform: translate3d(300px, -350px, 350px); // or another value you need
}
.si__single[data-aos="fade-up-right"] {
  // or another animation type you have
  -webkit-transform: translate3d(
    -300px,
    350px,
    350px
  ); // or another value you need
  transform: translate3d(-300px, 350px, 350px); // or another value you need
}

.section {
  min-height: 100vh;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  display: grid;
  overflow: hidden;

  &__wrapper {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 100%;

    @include min-width("xl") {
      flex-direction: row;
    }

    &-right {
      @include min-width("xl") {
        flex-direction: row-reverse;
      }

      .section__content {
        padding: 10%;
        z-index: 10;

        @include min-width("xl") {
          padding: 0 10% 0 0;
        }
      }
    }
  }
  &__content {
    padding: 10%;
    z-index: 10;

    @include min-width("xl") {
      width: 50%;
      padding: 0 0 0 10%;
    }

    .sc {
      &__title {
        margin: 20% 0 10%;

        @include min-width("xl") {
          margin: 0 0 50px 0;
        }
      }
      &__text {
        max-width: 512px;
        font-size: 16px;
        font-family: $font-family-text;
      }
    }
  }
  &__image {
    height: 100%;
    width: 100%;
    display: flex;
    align-items: center;

    @include min-width("xl") {
      width: 70%;
      max-width: 1099.64px;
      max-height: 937px;
    }

    .si {
      &__picture {
        background: linear-gradient(90deg, rgba(255, 255, 255, 0.85));
        background-size: contain;
        margin-bottom: 0;
      }
      &__elements {
        @include min-width("xl") {
          max-height: 937px;
        }
      }
    }
  }
}

$size: 100px;
$rotate: rotate(34deg);

.section {
  &--1 {
    position: relative;
    @import "sections/first";
  }
  &--2 {
    position: relative;
    @import "sections/second";
  }
  &--3 {
    position: relative;
    @import "sections/third";
  }
  &--4 {
    position: relative;
    @import "sections/fourth";
  }
  &--5 {
    position: relative;
    @import "sections/five";
  }
  &--6 {
    position: relative;

    @include min-width("xl") {
      .section__content {
        width: 60%;
      }
    }
    @import "sections/six";
  }
  &--7 {
    position: relative;
    @import "sections/sevem";
  }
  &--8 {
    position: relative;

    @include min-width("xl") {
      .section__content {
        width: 60%;
      }
    }
    @import "sections/eight";
  }
  &--9 {
    position: relative;

    @import "sections/nine";
  }
}
