.banner {
  min-height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  background: rgba($color: #000000, $alpha: 0.3);

  .fp-tableCell {
    display: flex;
  }

  &__video {
    position: absolute;
    z-index: -1;
    width: 100%;
    height: 100%;
    object-fit: cover;
  }

  &__wrapper {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
  }

  &__title {
    color: #fff;
    text-align: center;
    display: flex;
    flex-direction: column;
    margin-top: 2%;
    font-family: $font-family-heading;

    &--big {
      font-family: $font-family-second;
      font-size: 60px;
      line-height: 1;

      @include min-width("xl") {
        font-size: 80px;
        border-bottom: 2px solid #fff;
        padding-bottom: 12px;
      }
    }
    &--small {
      font-family: $font-family-second;
      font-size: 22px;

      @include min-width("xl") {
        font-size: 32px;
        letter-spacing: 4px;
      }
    }
  }
  &__logo {
    max-width: 50%;
    position: absolute;
    bottom: 0;
    right: 0;
    padding: 2rem;

    @include min-width("xl") {
      max-width: 90%;

      svg {
        width: 90%;
      }
    }
  }
}
