.news__stripe[data-aos="fade-down-left"] {
  // or another animation type you have
  -webkit-transform: translate3d(
    300px,
    -350px,
    350px
  ); // or another value you need
  transform: translate3d(300px, -350px, 350px); // or another value you need
}
.news__stripe[data-aos="fade-up-right"] {
  // or another animation type you have
  -webkit-transform: translate3d(
    -300px,
    350px,
    350px
  ); // or another value you need
  transform: translate3d(-300px, 350px, 350px); // or another value you need
}

.news {
  overflow: hidden;
  padding-bottom: 5%;

  &__title {
    font-size: 2rem;
    font-weight: semibold;
    text-align: center;
    line-height: 1;
    margin: 25px;

    @include min-width("xl") {
      font-size: 66px;
      margin: 0 0 60px 0;
    }
  }
  &__wrapper {
    padding: 5% 10%;
    position: relative;

    @include min-width("xl") {
      padding: 5% 15%;
    }
  }
  &__stripe {
    width: 130px;
    height: 600px;
    position: absolute;
    z-index: -1;

    .element {
      width: 100%;
      height: 100%;
      transform: rotate(35deg);
    }

    &--before {
      left: 3%;
      top: 25%;

      .element {
        background: linear-gradient(
          0deg,
          rgba(0, 73, 238, 1) 40%,
          rgba(#69bbfb, 0.7) 100%
        );
      }
    }
    &--after {
      right: -3%;
      top: 35%;

      .element {
        background: linear-gradient(
          0deg,
          rgba(#ebebff, 0.5) 0%,
          rgba(#aaabff, 0.7) 30%,
          rgba(0, 73, 238, 1) 100%
        );
      }
    }
  }
  &__items {
    @include min-width("xl") {
      display: grid;
      grid-template-columns: repeat(3, 1fr);
      grid-template-rows: repeat(2, 1fr);
      grid-column-gap: 10%;
      grid-row-gap: 10%;
    }
  }
  &__single {
    display: flex;
    flex-direction: column;
    align-items: center;
    background: rgba($color: #fff, $alpha: 1);
    margin: 0.6rem 0;
    box-shadow: 0 0 5px 1px $color-primary;
    padding: 0.5rem;

    @include min-width("xl") {
      background: none;
      box-shadow: none;
    }

    .ns {
      &__image {
        display: none;
        flex-direction: column;
        align-items: center;
        height: 60%;
        margin-bottom: 0;

        @include min-width("xl") {
          display: flex;
        }

        img {
          width: 100%;
          object-fit: cover;
          height: 100%;
        }
      }
      &__site {
        font-size: 14px;
        font-family: $font-family-text;

        @include min-width("xl") {
          margin: 10px 0;
          font-size: 16px;
        }
      }
      &__content {
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        width: 100%;
      }
      &__title {
        font-size: 20px;
        color: #5f5f5f;
        margin-bottom: 0.5rem;
        margin-top: 0.5rem;
        line-height: 1;
        font-weight: 700;

        @include min-width("xl") {
          font-size: 30px;
          line-height: 1.1;
          margin-bottom: 0;
          margin-top: 0;
        }
      }
      &__text {
        font-size: 16px;
        margin-bottom: 1.7em;

        @include min-width("xl") {
          font-size: 20px;
        }

        p {
          margin-bottom: 0;
        }
      }
      &__button {
        width: 100%;
        justify-content: flex-end;
        display: flex;

        @include min-width("xl") {
          justify-content: flex-start;
        }
        a {
          display: flex;
          flex-direction: row;
          align-items: center;
          justify-content: center;
          font-size: 12px;
          font-weight: 700;
          font-family: $font-family;

          svg {
            max-width: 20px;
            margin-left: 10px;
          }

          &:hover {
            span {
              color: black;
            }
          }
        }
      }
    }

    @include min-width("xl") {
      &--1 {
        grid-area: 1 / 2 / 2 / 3;
      }
      &--2 {
        grid-area: 1 / 3 / 3 / 4;
      }
      &--4 {
        grid-area: 2 / 2 / 3 / 3;
      }
      &--3 {
        grid-area: 1 / 1 / 3 / 2;
      }
    }
  }
}
