.news__stripe[data-aos=fade-down-left] {
  -webkit-transform: translate3d(300px, -350px, 350px);
  transform: translate3d(300px, -350px, 350px);
}

.news__stripe[data-aos=fade-up-right] {
  -webkit-transform: translate3d(-300px, 350px, 350px);
  transform: translate3d(-300px, 350px, 350px);
}

.news {
  overflow: hidden;
  padding-bottom: 5%;
}
.news__title {
  font-size: 2rem;
  font-weight: semibold;
  text-align: center;
  line-height: 1;
  margin: 25px;
}
@media (min-width: 1200px) {
  .news__title {
    font-size: 66px;
    margin: 0 0 60px 0;
  }
}
.news__wrapper {
  padding: 5% 10%;
  position: relative;
}
@media (min-width: 1200px) {
  .news__wrapper {
    padding: 5% 15%;
  }
}
.news__stripe {
  width: 130px;
  height: 600px;
  position: absolute;
  z-index: -1;
}
.news__stripe .element {
  width: 100%;
  height: 100%;
  transform: rotate(35deg);
}
.news__stripe--before {
  left: 3%;
  top: 25%;
}
.news__stripe--before .element {
  background: linear-gradient(0deg, #0049ee 40%, rgba(105, 187, 251, 0.7) 100%);
}
.news__stripe--after {
  right: -3%;
  top: 35%;
}
.news__stripe--after .element {
  background: linear-gradient(0deg, rgba(235, 235, 255, 0.5) 0%, rgba(170, 171, 255, 0.7) 30%, #0049ee 100%);
}
@media (min-width: 1200px) {
  .news__items {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-template-rows: repeat(2, 1fr);
    grid-column-gap: 10%;
    grid-row-gap: 10%;
  }
}
.news__single {
  display: flex;
  flex-direction: column;
  align-items: center;
  background: white;
  margin: 0.6rem 0;
  box-shadow: 0 0 5px 1px #4a4a4a;
  padding: 0.5rem;
}
@media (min-width: 1200px) {
  .news__single {
    background: none;
    box-shadow: none;
  }
}
.news__single .ns__image {
  display: none;
  flex-direction: column;
  align-items: center;
  height: 60%;
  margin-bottom: 0;
}
@media (min-width: 1200px) {
  .news__single .ns__image {
    display: flex;
  }
}
.news__single .ns__image img {
  width: 100%;
  object-fit: cover;
  height: 100%;
}
.news__single .ns__site {
  font-size: 14px;
  font-family: "Open Sans", sans-serif;
}
@media (min-width: 1200px) {
  .news__single .ns__site {
    margin: 10px 0;
    font-size: 16px;
  }
}
.news__single .ns__content {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  width: 100%;
}
.news__single .ns__title {
  font-size: 20px;
  color: #5f5f5f;
  margin-bottom: 0.5rem;
  margin-top: 0.5rem;
  line-height: 1;
  font-weight: 700;
}
@media (min-width: 1200px) {
  .news__single .ns__title {
    font-size: 30px;
    line-height: 1.1;
    margin-bottom: 0;
    margin-top: 0;
  }
}
.news__single .ns__text {
  font-size: 16px;
  margin-bottom: 1.7em;
}
@media (min-width: 1200px) {
  .news__single .ns__text {
    font-size: 20px;
  }
}
.news__single .ns__text p {
  margin-bottom: 0;
}
.news__single .ns__button {
  width: 100%;
  justify-content: flex-end;
  display: flex;
}
@media (min-width: 1200px) {
  .news__single .ns__button {
    justify-content: flex-start;
  }
}
.news__single .ns__button a {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  font-size: 12px;
  font-weight: 700;
  font-family: "Lato", sans-serif;
}
.news__single .ns__button a svg {
  max-width: 20px;
  margin-left: 10px;
}
.news__single .ns__button a:hover span {
  color: black;
}
