  .news__single--1 {
    grid-area: 1/2/2/3;
  }
  .news__single--2 {
    grid-area: 1/3/3/4;
  }
  .news__single--4 {
    grid-area: 2/2/3/3;
  }
  .news__single--3 {
    grid-area: 1/1/3/2;
